/*css add by rohit kumar*/
.about_us { width: 100%; padding-bottom: 40px; }
.about_us .container { max-width: 1170px !important; }
.about_us p { font-size: 16px !important; color:#565656; line-height: 28px;}
.about_us h1 { text-transform: uppercase; text-align: center; font-weight: 700; font-size: 25px !important; padding-bottom: 10px; margin-bottom: 40px; position: relative; }
.about_us h1:before { content: ""; position: absolute; display: inline-block; width: 50px; height: 2px; bottom: 0px; left: 50%; background:#0681cb; transform: translateX(-50%); -moz-transform: translateX(-50%); -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%); }
.about_us .about_comp { width: 100%; margin-bottom: 50px; }
.about_us .about_comp p { font-size: 16px !important; line-height: 22px; }
.about_us .about_comp p strong { font-weight: 600; }
.about_us .about_comp h2 { font-size: 22px !important; line-height: 24px; text-transform: uppercase; text-align: left; margin-bottom: 10px; color:#333; }
.about_us .about_comp .s_list { width: 100%; margin-bottom: 30px; overflow:hidden; }
.about_us .about_comp ul { margin: 0px -15px; padding-left: 0px; }
.about_us .about_comp ul li { float: left; padding: 0px 15px; text-align: center; width: 16.66%; margin-bottom: 10px; }
.about_us .about_comp ul li .img { display: inline-block; margin-bottom: 10px; }
.about_us .about_comp ul li .img img { display: inline-block; }
.about_us .about_comp ul li p { margin-bottom: 0px !important; text-align: center; font-size: 14px !important; }
.about_us .about_comp .box { width: 100%; overflow: hidden; margin-bottom: 20px; }
.about_us .about_comp h5 { font-size: 18px !important; font-weight: 600; margin: 0px 0px 0px; padding: 0px; border: 0px; background: none; }

.about_us .about_memb { width: 100%; }
.about_us .about_memb h2 { text-align: center; margin-bottom: 40px; }
.about_us .about_memb .memb_list { width: 100%; overflow:hidden; text-align: center; }
.about_us .about_memb .memb_list ul { margin: 0px -25px; padding: 0px;  }
.about_us .about_memb .memb_list ul li { padding: 0px 25px; display: inline-block; width: 25%; }
.about_us .about_memb .memb_list ul li .box { text-align: center; width: 100%; overflow: hidden; }
.about_us .about_memb .memb_list ul li .box a { display: inline-block; width: 70%; overflow: hidden; border-radius: 50%; }
.about_us .about_memb .memb_list img { width: 100%; border-radius: 50%; overflow: hidden; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.about_us .about_memb .memb_list ul li .box a:hover img { transform: scale(1.1); -moz-transform: scale(1.1); -webkit-transform: scale(1.1); -ms-transform: scale(1.1); transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.about_us .about_memb .memb_list .box h4 { font-size: 20px; margin-bottom: 0px; }
.about_us .about_memb .memb_list .box h4 span { display: inline-block; width: 100%; font-size: 15px; font-weight: 600; }
.about_us .about_memb .memb_list .box p { font-size: 15px !important; line-height: 22px;text-align: center; }
.about_us .about_memb .row { margin-bottom: 40px; }


.about_us_owner { background:rgba(0, 0, 0, 0.5); overflow-y: auto; }
.about_us_owner::-webkit-scrollbar-track {-webkit-box-shadow: none; background-color: #ccc; }
.about_us_owner::-webkit-scrollbar{width: 1px;background-color: #fff;  }
.about_us_owner::-webkit-scrollbar-thumb {background-color: #777; border: 1px solid #444;  }
.about_us_owner .about_owner_list { max-width: 500px; min-height: 300px; width: 500px; padding: 25px 25px; position: absolute; top: 50%; left: 50%; margin: 0 auto; border-radius: 10px; background:#fff; transform: translate(-50%, -50%); -moz-transform: translate(-50%, -50%); -webkit-transform: translate(-50%, -50%); -ms-transform: translate(-50%, -50%);  }
.about_us_owner .about_owner_list .box { text-align: center; width: 100%; overflow: hidden; }
.about_us_owner .about_owner_list .box .img { display: inline-block; width: 150px; overflow: hidden; border-radius: 50%; }
.about_us_owner .about_owner_list .box .img img { width: 100%; border-radius: 50%; overflow: hidden; transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -webkit-transition: 0.5s ease-in-out; -ms-transition: 0.5s ease-in-out; }
.about_us_owner .about_owner_list .box h4 { font-size: 20px; margin-bottom: 0px; }
.about_us_owner .about_owner_list .box h4 span { display: inline-block; width: 100%; font-size: 15px; font-weight: 600; }
.about_us_owner .about_owner_list .box p { font-size: 15px !important; line-height: 22px; text-align: center; }
.about_us_owner .about_owner_list .box .desc { width: 100%; overflow:hidden; }
.about_us_owner .about_owner_list .box .desc p { margin-bottom: 0px; font-size: 12px !important; line-height: 15px; }

